import React, { useState, useEffect, useRef } from 'react';
import { getDatabase, ref, runTransaction } from 'firebase/database';
import DrawModal from './DrawModal';
import '../App.css';

//  ()
const CHART_DATA = [
  { row: '', chars: [{ char: '', romaji: 'a' }, { char: '', romaji: 'i' }, { char: '', romaji: 'u' }, { char: '', romaji: 'e' }, { char: '', romaji: 'o' }] },
  { row: 'K', chars: [{ char: '', romaji: 'ka' }, { char: '', romaji: 'ki' }, { char: '', romaji: 'ku' }, { char: '', romaji: 'ke' }, { char: '', romaji: 'ko' }] },
  { row: 'S', chars: [{ char: '', romaji: 'sa' }, { char: '', romaji: 'shi' }, { char: '', romaji: 'su' }, { char: '', romaji: 'se' }, { char: '', romaji: 'so' }] },
  { row: 'T', chars: [{ char: '', romaji: 'ta' }, { char: '', romaji: 'chi' }, { char: '', romaji: 'tsu' }, { char: '', romaji: 'te' }, { char: '', romaji: 'to' }] },
  { row: 'N', chars: [{ char: '', romaji: 'na' }, { char: '', romaji: 'ni' }, { char: '', romaji: 'nu' }, { char: '', romaji: 'ne' }, { char: '', romaji: 'no' }] },
  { row: 'H', chars: [{ char: '', romaji: 'ha' }, { char: '', romaji: 'hi' }, { char: '', romaji: 'fu' }, { char: '', romaji: 'he' }, { char: '', romaji: 'ho' }] },
  { row: 'M', chars: [{ char: '', romaji: 'ma' }, { char: '', romaji: 'mi' }, { char: '', romaji: 'mu' }, { char: '', romaji: 'me' }, { char: '', romaji: 'mo' }] },
  { row: 'Y', chars: [{ char: '', romaji: 'ya' }, { char: null, romaji: '' }, { char: '', romaji: 'yu' }, { char: null, romaji: '' }, { char: '', romaji: 'yo' }] },
  { row: 'R', chars: [{ char: '', romaji: 'ra' }, { char: '', romaji: 'ri' }, { char: '', romaji: 'ru' }, { char: '', romaji: 're' }, { char: '', romaji: 'ro' }] },
  { row: 'W', chars: [{ char: '', romaji: 'wa' }, { char: null, romaji: '' }, { char: null, romaji: '' }, { char: null, romaji: '' }, { char: '', romaji: 'wo' }] },
  { row: 'N', chars: [{ char: '', romaji: 'n' }, { char: null, romaji: '' }, { char: null, romaji: '' }, { char: null, romaji: '' }, { char: null, romaji: '' }] }
];

const HiraganaFillGame = ({ username, onBack }) => {
  const [difficulty, setDifficulty] = useState(null);
  const [gridState, setGridState] = useState([]);
  const [completed, setCompleted] = useState(false);
  const [score, setScore] = useState(0);
  
  // State  ( isDrawMode  )
  const [activeCell, setActiveCell] = useState(null); 

  // Timer State
  const [timeLeft, setTimeLeft] = useState(0); 
  const [gameActive, setGameActive] = useState(false);
  const timerRef = useRef(null);

  useEffect(() => {
    if (difficulty) {
      initGame(difficulty);
    }
    return () => clearInterval(timerRef.current);
  }, [difficulty]);

  useEffect(() => {
    if (gameActive && timeLeft > 0) {
      timerRef.current = setInterval(() => {
        setTimeLeft((prev) => prev - 1);
      }, 1000);
    } else if (timeLeft === 0 && gameActive) {
      clearInterval(timerRef.current);
      setGameActive(false);
      alert(" !");
      setDifficulty(null);
    }
    return () => clearInterval(timerRef.current);
  }, [gameActive, timeLeft]);

  const initGame = (selectedDiff) => {
    let initialGrid = [];
    let timeLimit = 300; 
    if (selectedDiff === 'hard') timeLimit = 240; 
    if (selectedDiff === 'master') timeLimit = 180; 

    setTimeLeft(timeLimit);
    setGameActive(true);

    CHART_DATA.forEach((row, rIndex) => {
      let rowData = [];
      row.chars.forEach((item, cIndex) => {
        if (!item.char) {
          rowData.push({ ...item, type: 'empty' });
        } else {
          let isHidden = false;
          // Normal:  50%, Hard/Master:  100%
          if (selectedDiff === 'normal') isHidden = Math.random() < 0.5;
          else isHidden = true;

          rowData.push({
            ...item,
            rIndex, cIndex,
            isHidden: isHidden,
            isCorrect: !isHidden, 
            userInput: '' //  ''
          });
        }
      });
      initialGrid.push(rowData);
    });

    setGridState(initialGrid);
    setCompleted(false);
    setScore(0);
  };

  //   (Self-Check Passed)
  const handleDrawSuccess = () => {
    if (!activeCell) return;
    const { rIndex, cIndex } = activeCell;
    
    const newGrid = [...gridState];
    const cell = newGrid[rIndex][cIndex];

    cell.isCorrect = true;
    cell.isHidden = false;
    cell.userInput = ''; // 

    setScore(prev => prev + 10);
    // 
    if (difficulty !== 'normal') setTimeLeft(prev => prev + 2);
    
    setGridState(newGrid);
    setActiveCell(null);
    checkCompletion(newGrid);
  };

  const checkCompletion = (currentGrid) => {
    const allCorrect = currentGrid.every(row => 
      row.every(cell => cell.type === 'empty' || cell.isCorrect)
    );
    if (allCorrect && !completed) {
      setCompleted(true);
      setGameActive(false);
      clearInterval(timerRef.current);
      giveRewards();
    }
  };

  const giveRewards = () => {
    if (username && username !== "Guest") {
      const db = getDatabase();
      const userRef = ref(db, `users/${username}/coins`);
      let bonus = 100;
      if (difficulty === 'hard') bonus = 300;
      if (difficulty === 'master') bonus = 500;

      runTransaction(userRef, (currentCoins) => (currentCoins || 0) + bonus);
    }
  };

  // UI 
  if (!difficulty) {
    return (
      <div className="game-container" style={{ maxWidth: '600px', textAlign: 'center' }}>
         <button onClick={onBack} className="back-btn"> </button>
         <h2 style={{ fontSize: '2rem', marginBottom: '20px' }}> </h2>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '15px' }}>
          {['normal', 'hard', 'master'].map(diff => (
            <button key={diff} className={`diff-btn ${diff}`} onClick={() => setDifficulty(diff)}>
              <span style={{fontSize:'24px'}}>{diff === 'normal' ? '' : diff === 'hard' ? '' : ''}</span>
              <div style={{textTransform: 'capitalize'}}>
                <strong>{diff}</strong><br/>
                <small style={{color: '#666', fontWeight: 'normal'}}>
                  {diff === 'normal' ? ' ()' : diff === 'hard' ? ' ()' : ' (!)'}
                </small>
              </div>
            </button>
          ))}
        </div>
      </div>
    );
  }

  // UI  (Drawing Only)
  return (
    <div className="game-container" style={{ maxWidth: '850px' }}>
      
      {/* Modal  */}
      {activeCell && (
        <DrawModal 
          targetChar={activeCell.char}
          targetRomaji={activeCell.romaji}
          onClose={() => setActiveCell(null)}
          onCorrect={handleDrawSuccess}
        />
      )}

      <div className="header-nav" style={{justifyContent: 'space-between', alignItems: 'center'}}>
        <button onClick={() => setDifficulty(null)} className="back-btn" style={{fontSize: '14px'}}> </button>
        
        <div style={{textAlign: 'center'}}>
           <div style={{ fontSize: '14px', color: '#888' }}>
             : {difficulty.toUpperCase()}
           </div>
           <h2 style={{margin: '5px 0'}}> </h2>
        </div>

        <div className={`timer-box ${timeLeft < 30 ? 'danger' : ''}`}>
            {Math.floor(timeLeft / 60)}:{(timeLeft % 60).toString().padStart(2, '0')}
        </div>
      </div>

      <div className="hiragana-grid" style={{marginTop: '20px'}}>
        <div className="grid-header"></div>
        {['a', 'i', 'u', 'e', 'o'].map((h, i) => (
          <div key={i} className="grid-header">{difficulty === 'master' ? '?' : h}</div>
        ))}

        {gridState.map((row, rIndex) => (
          <React.Fragment key={rIndex}>
            <div className="row-label">{difficulty === 'master' ? '?' : CHART_DATA[rIndex].row}</div>
            
            {row.map((cell, cIndex) => {
              if (cell.type === 'empty') return <div key={cIndex} className="grid-cell empty"></div>;

              return (
                <div 
                  key={cIndex} 
                  className={`grid-cell ${cell.isCorrect ? 'correct' : 'pending'}`}
                  //   ()
                  onClick={() => {
                    if (cell.isHidden && gameActive) {
                      setActiveCell(cell);
                    }
                  }}
                  style={{ 
                    cursor: (cell.isHidden && gameActive) ? 'pointer' : 'default',
                    position: 'relative'
                  }}
                >
                  {cell.isHidden ? (
                    //  ?   
                    <div style={{opacity: 0.4, fontSize: '24px'}}>
                       {difficulty === 'normal' ? '?' : ''}
                    </div>
                  ) : (
                    <span className="grid-char">{cell.char}</span>
                  )}
                </div>
              );
            })}
          </React.Fragment>
        ))}
      </div>
      <div style={{height: '50px'}}></div>
    </div>
  );
};

export default HiraganaFillGame;